.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

footer {
  width: auto;
  background: #282c34;

}

.footPart {
  display: inline-block;
  align-content: left;
  width: 30vw;
}

.footPart h3 {
  color: #FFF
}

.footPart h4 {
  color: lightblue;
}

ul {
  position: relative;
}

li {
  display: inline;
  margin: auto 5vw;
  align-content: left;
}

input {
  text-align: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
